<template>
  <div class="flex flex-col gap-spacing-200">
    <GeneralButton
      :button-type="'button'"
      :type="'success'"
      :size="'small'"
      :emphasis="'secondary'"
      :icon-position="'none'"
      :label="'Save'"
      @click.native="save"
    />
    <div ref="spreadsheet"></div>
  </div>
</template>

<script>
import "jspreadsheet-ce/dist/jspreadsheet.css";
import jspreadsheet from "jspreadsheet-ce";
import { FormValidationRule } from "@/utilities/helpers/formValidations";

export default {
  name: "SpreadsheetComponent",
  data() {
    return {
      errorTracker: {}, // Map to track cells with errors
      fieldValidations: {
        companyName: new FormValidationRule({
          minLength: 5,
          maxLength: 50,
          required: false,
        }),
        generalNumber: new FormValidationRule({
          minLength: 19,
          maxLength: 19,
          required: false,
        }),
        commercialNumber: new FormValidationRule({
          minLength: 19,
          maxLength: 19,
          required: false,
        }),
        email: new FormValidationRule({
          type: "EMAIL",
          required: false,
        }),
        annualRevenue: new FormValidationRule({
          type: "NUMBER",
          min: 0,
        }),
      },
    };
  },
  mounted() {
    this.$bus.$on("clear-spreadsheet-data", () => {
      const spreadsheet = this.$refs.spreadsheet.jexcel;
      if (spreadsheet) {
        const columns = spreadsheet.getConfig().columns;

        // Create an empty dataset with the same number of columns
        const emptyData = Array.from({ length: 60 }, () =>
          Array.from({ length: columns.length }, () => ""),
        );

        // Reset the data
        spreadsheet.setData(emptyData);
      }
    });
    this.initSpreadsheet();
  },
  components: {
    GeneralButton: () => import("@/components/General/CustomButton.vue"),
  },
  methods: {
    initSpreadsheet() {
      const columns = [
        { title: "Company Name", type: "text", width: 300, key: "companyName" },
        {
          title: "General Number",
          type: "number",
          width: 200,
          key: "generalNumber",
        },
        {
          title: "Commercial Number",
          type: "number",
          width: 200,
          key: "commercialNumber",
        },
        { title: "Email", type: "text", width: 200, key: "email" },
        {
          title: "Annual Revenue",
          type: "number",
          width: 200,
          key: "annualRevenue",
        },
        {
          title: "Add to Main DB", // New checkbox column
          type: "checkbox",
          width: 150,
          key: "addToMainDB",
        },
      ];

      jspreadsheet(this.$refs.spreadsheet, {
        columns,
        columnSorting: false,
        allowDeleteColumn: false,
        allowDeletingAllRows: false,
        allowInsertColumn: false,
        allowRenameColumn: false,
        allowManualInsertColumn: false,
        about: false,
        csvFileName: "Company Data",
        minDimensions: [6, 10],
        wordWrap: true,

        data: Array.from({ length: 60 }, () =>
          Array.from({ length: columns.length }, (_, index) => {
            if (index === 1 || index === 2) {
              return "+0000 000 00 00 000";
            }
          }),
        ),
        onbeforechange: (instance, cell, colIndex, rowIndex, newValue) => {
          const columnIndex = cell.getAttribute("data-x"); // Get column index
          const columnKey = columns[columnIndex]?.key; // Get column's key from the config
          const validator = this.fieldValidations[columnKey]; // Get the validation rule for this column

          // Auto-format logic for specific columns
          if (["generalNumber", "commercialNumber"].includes(columnKey)) {
            const formattedValue = this.formatPhoneNumber(newValue);

            if (newValue !== formattedValue) {
              this.$refs.spreadsheet?.jexcel?.setValue(cell, formattedValue);
            }
            return formattedValue;
          }

          if (validator) {
            const validationError = validator?.validate(newValue); // Validate the new value
            const cellKey = `${rowIndex}-${colIndex}`;

            if (validationError) {
              this.errorTracker[cellKey] = validationError;
              // If validation fails, color the cell red and reject the change
              cell.style.backgroundColor = "rgba(255, 0, 0, 0.6)";
              cell.setAttribute("title", `Error: ${validationError}`);
            } else {
              this.errorTracker[cellKey] = undefined;
              delete this.errorTracker[cellKey];
              // If validation passes, reset the cell's background color
              cell.style.backgroundColor = "";
              cell.removeAttribute("title");
            }
          }
        },
      });
    },
    getSpreadsheetData() {
      // Access the spreadsheet data and return it as JSON
      const spreadsheet = this.$refs.spreadsheet.jexcel;
      const data = spreadsheet.getData();
      const columns = spreadsheet.getConfig().columns;

      // Convert data to JSON format with camelCase keys and filter out rows with empty "companyName"
      const jsonData = data
        .map((row) => {
          const rowObject = columns.reduce((acc, col, index) => {
            acc[col.key] = row[index];
            return acc;
          }, {});

          // Only include rows where "companyName" exists and is not empty
          return rowObject.companyName ? rowObject : null;
        })
        .filter((row) => row !== null); // Remove null entries

      return jsonData;
    },
    hasErrors() {
      // Check if the errorTracker Map has any entries
      return Object.keys(this.errorTracker)?.length > 0;
    },
    save() {
      if (this.hasErrors()) {
        this.$toast.error("Cannot save. Some cells have errors.");
        return;
      }
      const jsonData = this.getSpreadsheetData();
      this.$emit("spreadsheet-data", jsonData);
    },
    formatPhoneNumber(value) {
      if (!value) return "+0000 000 00 00 000";

      // Ensure we only keep digits
      const digits = value.replace(/\D/g, "");

      // Format digits to +0000 000 00 00 000
      const formatted = "+0000 000 00 00 000".split("");
      let digitIndex = 0;

      for (let i = 0; i < formatted.length; i++) {
        if (formatted[i] === "0" && digitIndex < digits.length) {
          formatted[i] = digits[digitIndex++];
        }
      }

      return formatted.join("");
    },
  },
  beforeDestroy() {
    this.$bus.$off("clear-spreadsheet-data");
  },
};
</script>

<style>
.jexcel_overflow {
  overflow-y: auto;
  overflow-x: auto;
}
.jexcel_about {
  display: none;
}
</style>
